.Rent {
   flex: 1 1 auto;
   padding-top: 160px;
   opacity: 0;
   animation-name: fadein;
   animation-duration: 1.5s;
   animation-fill-mode: forwards;
}

.RentObjects_container {
   position: relative;
}

.RentObjects-enter {
   opacity: 0;
   position: absolute;
   top: 0;
   left: 0;
}

.RentObjects-enter-active {
   opacity: 1;
   transition: all 1000ms ease-in-out;
}

.RentObjects-exit {
   opacity: 1;
}

.RentObjects-exit-active {
   opacity: 0;
   transition: all 100ms ease-in-out;
}